html, body {
    box-sizing: border-box;
    margin-left: auto ;
    margin-right: auto ;
}

html {
    min-height: 100vh ;
    display: flex ;
    align-items: start ;
    justify-content: stretch ;
}

body {
    width: 100%;
    position: relative ;
    display: block ;
    align-items: stretch ;
    justify-content: stretch ;
    row-gap: $line;
    column-gap: $col2;
    flex-direction: column ;
    grid-template-columns: 1fr [main] auto [aside] auto 1fr ;
    grid-auto-flow: dense ;
}

body > * {
    grid-column: main;
}
body > header {
    display: flex ;
    justify-content: end ;
    grid-column: span 2;
}
body > footer {
    grid-column: aside;
}

@media screen {
    body {
        margin: 0 0.2em ;
        margin-bottom: $lineandhalf ;
        margin-top: 0.2em ;
    }
}

@media screen and (min-width: $body-width + $A4-margin) {
    body {
        padding: 0;
        display: grid ;
        grid-template-columns: [main] $body-width [aside] $aside ;
    }
    body > header {
        justify-self: end ;
    }
}
@media screen and (min-width: $A4-width + $A4-margin) {
    body {
        margin-top: $A4-margin ;
        margin-bottom: $A4-margin ;
        margin-left: $A4-margin ;
    }
    html {
        justify-content: start ;
    }
}

body > main > nav {
    display: flex ;
    flex-wrap: wrap ;
    justify-content: space-between ;
}
